.topnav {
  position: fixed;
  display: flex;
  top: 0;
  left: 0%;
  right: 0%;
  width: 100%;
  z-index: 100;
  font-size: 32px;
  padding: 16px;
  justify-content: space-evenly;
  background-color: #01121897;
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
}

.topnav a {
  padding: 14px;
  text-decoration: none;
  border-radius: 20px;
  color: rgba(251, 251, 251, 0.921);
}

.topnav a:hover{
    color: rgba(32, 135, 191, 0.452);
}

.sepTopNav{
  width: 100%;
}
